<script>
import Swiper, { Pagination } from 'swiper';
import getAxiosHeaders from '@/assets/js/getAxiosHeaders';
import popularSwiper from '@/components/PopularSwiper.vue';

Swiper.use([Pagination]);
export default {
  data() {
    return {
      citySwiper: [],
      randomNumArr: [0, 1, 2, 3],
      scenicSpotArr: [],
      restaurantArr: [],
      activityArr: [],
    };
  },
  components: {
    popularSwiper,
  },
  methods: {
    getAllData() {
      this.$http.get('https://ptx.transportdata.tw/MOTC/v2/Tourism/ScenicSpot?$format=JSON', getAxiosHeaders())
        .then((res) => {
          this.scenicSpotData = res.data;
          this.randomData(this.scenicSpotData, this.scenicSpotArr);
        })
        .catch((err) => {
          console.dir(err);
        });
      this.$http.get('https://ptx.transportdata.tw/MOTC/v2/Tourism/Restaurant?$format=JSON', getAxiosHeaders())
        .then((res) => {
          this.restaurantData = res.data;
          this.randomData(this.restaurantData, this.restaurantArr);
        })
        .catch((err) => {
          console.dir(err);
        });
      this.$http.get('https://ptx.transportdata.tw/MOTC/v2/Tourism/Activity?$format=JSON', getAxiosHeaders())
        .then((res) => {
          this.activityData = res.data;
          this.randomData(this.activityData, this.activityArr);
        })
        .catch((err) => {
          console.dir(err);
        });
    },
    randomData(data, arr) {
      const num = data.length;
      this.randomNumArr.forEach(() => {
        const radomNum = Math.ceil(Math.random() * num);
        arr.push(data[radomNum - 1]);
      });
    },
    renderSwiper() {
      this.citySwiper = new Swiper(this.$refs.citySwiper, {
        slidesPerView: 2.5,
        spaceBetween: 10.5,
        breakpoints: {
          // when window width is >= 430px
          430: {
            slidesPerView: 3.5,
            spaceBetween: 15,
          },
        },
      });
    },
    toCity(cityEn, cityName) {
      console.log(this.$router, cityEn);
      this.$router.push(`/city/${cityEn}?cityName=${cityName}`);
    },
  },
  mounted() {
    this.getAllData();
    this.renderSwiper();
  },
};
</script>
