<script>
import Swiper, { Pagination } from 'swiper';

Swiper.use([Pagination]);
export default {
  data() {
    return {
      popularSwiper: [],
      scenicSpotData: [],
      restaurantData: [],
      activityData: [],
    };
  },
  props: ['data'],
  methods: {
    renderSwiper() {
      const popularSwiperOption = {
        slidesPerView: 1.2,
        spaceBetween: 15,
        breakpoints: {
          // when window width is >= 430px
          500: {
            slidesPerView: 2.2,
            spaceBetween: 20,
          },
          768: {
            slidesPerView: 3.2,
            spaceBetween: 20,
          },
          992: {
            slidesPerView: 4,
            spaceBetween: 20,
          },
        },
        pagination: {
          el: this.$refs.swiperPagination,
          clickable: true,
        },
      };
      this.popularSwiper = new
      Swiper(this.$refs.popularSwiper, popularSwiperOption);
    },
    toDetailedPage(id) {
      this.$router.push(`/detailed/${id}`);
    },
  },
  mounted() {
    this.renderSwiper();
  },
};
